@import "../main.scss";

/* Foamboard Theme */

:global(#content) {
  background: url("./images/foam.png") repeat #5B81C5;
}

:global(.foamboard) {

  .header {
    background: inherit;

    .info {
      color: white;

      .name,
      .description {
        opacity: 1;
        text-shadow: 0 2px 4px rgba(black, .3);
      }

      .name {
        font-size: 28px;
      }

      .description {
        font-size: 20px;
      }

    }

  }

  .board {

    .card-wrapper {
      @include transform(rotate(1.5deg) scale(1.025));
      padding-top: 10px;

      &:nth-child(3n+1) {
        @include transform(rotate(-1deg) scale(1.025));
      }

    }

    .card {
      background: #B94F47;
      border-radius: 4px;
      z-index: 1;
      box-shadow: 0 5px 10px rgba(black, 0.5);

      &:after {
        @include position(absolute, -5px 0 auto 0);
        @include size(15px);
        margin: auto;
        background: url("./images/pin.png") center no-repeat;
        background-size: contain;
        content: "";
        z-index: 2;
      }

      .container {
        margin-top: 5px;
        background: inherit;
        color: white;
        border: none;
        position: relative;

        .top,
        .content,
        .bottom {
          position: relative;
          z-index: 2;
        }

        .top {

          .delete,
          .restore {
            @include transition(all 200ms);
            opacity: 0.5;

            :global(.icon) {

              path {
                fill: white;
              }

            }

            &:hover {
              opacity: 0.75;
            }

          }

        }

        .bottom {

          .like {
            opacity: 0.5;

            .like-counter {
              @include display(flex);
              margin-right: 5px;
              color: inherit;
            }

            :global(.icon) {

              path {
                fill: white;
              }

            }

            &:global(.liked) {
              opacity: 1;
            }

            &:hover {
              opacity: 0.75;
            }

          }

        }

      }

      &:nth-child(even):after {
        @include transform(scaleX(-1));
      }

    }

  }


}



