html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

@font-face {
  font-family: "Avenir";
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/avenir/AvenirLTStd-Light.woff") format("woff");
}

@font-face {
  font-family: "Avenir";
  font-style: normal;
  font-weight: 300;
  src: url("/fonts/avenir/AvenirLTStd-Roman.woff") format("woff");
}

@font-face {
  font-family: "Avenir";
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/avenir/AvenirLTStd-Heavy.woff") format("woff");
}

@font-face {
  font-family: "Eraser";
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/eraser/Eraser-Regular.eot?#iefix") format("embedded-opentype"), url("/fonts/eraser/Eraser-Regular.woff2") format("woff2"), url("/fonts/eraser/Eraser-Regular.woff") format("woff"), url("/fonts/eraser/Eraser-Regular.ttf") format("truetype"), url("/fonts/eraser/Eraser-Regular.svg#Eraser") format("svg");
}

@font-face {
  font-family: "Eraser Dust";
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/eraser-dust/Eraser-Dust-Regular.eot?#iefix") format("embedded-opentype"), url("/fonts/eraser-dust/Eraser-Dust-Regular.woff2") format("woff2"), url("/fonts/eraser-dust/Eraser-Dust-Regular.woff") format("woff"), url("/fonts/eraser-dust/Eraser-Dust-Regular.ttf") format("truetype"), url("/fonts/eraser-dust/Eraser-Dust-Regular.svg#Eraser Dust") format("svg");
}

@font-face {
  font-family: "Girls Have Many Secrets";
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/ghms/Girls-Have-Many-Secrets-Regular.eot?#iefix") format("embedded-opentype"), url("/fonts/ghms/Girls-Have-Many-Secrets-Regular.woff2") format("woff2"), url("/fonts/ghms/Girls-Have-Many-Secrets-Regular.woff") format("woff"), url("/fonts/ghms/Girls-Have-Many-Secrets-Regular.ttf") format("truetype"), url("/fonts/ghms/Girls-Have-Many-Secrets-Regular.svg#Girls Have Many Secrets") format("svg");
