@import "../../styles/main.scss";

.container {
  $gray1: #5B6D79;
  $gray2: #B8C0C6;

  @include wrapper;
  min-height: 100vh;
  overflow: auto;

  .form {
    @include display(flex);
    @include flex-direction(column);
    margin: 40px 0;
  }

  .row {
    @include display(flex);
    @include justify-content(flex-start);

    &:global(.header) {
      @include align-items(center);
    }

    &:global(.inputs) {
      @include align-items(stretch);
    }

    &:global(.vertical) {
      @include flex-direction(column);
      @include align-items(center);
    }

    &:global(.buttons) {
      @include justify-content(flex-end);
    }

    & + * {
      margin-top: 20px;
    }
  }

  .sheet-icon {
    @include display(flex);
    @include flex(0 0 24px);
    margin-right: 10px;
  }

  .title {
    @include display(flex);
    @include flex-grow(1);
    margin: 0;
    color: $gray1;
    font-size: 16px;
    font-weight: 400;
  }

  .name {
    @include display(flex);
    @include flex-grow(1);
    padding: 10px;
    margin-right: 20px;
    border: solid 1px $gray2;
    border-radius: 3px;
  }

  .image {
    @include display(flex);
    @include flex(0 0 50px);
    border: solid 1px currentcolor;
    border-radius: 3px;
    background: white;
    color: $gray2;
  }

  .description {
    @include display(flex);
    @include size(100% 100px);
    padding: 10px;
    border: solid 1px $gray2;
    border-radius: 3px;
    font-size: 14px;
    resize: none;
  }

  .subtitle {
    @include display(flex);
    @include flex-grow(1);
    @include size(100% auto);
    font-size: 14px;
    font-weight: 400;
  }

}

