@import "../../styles/main.scss";

.header {
  @include position(absolute, 0 0 auto 0);
  @include display(flex);
  @include align-items(center);
  @include size(100% $header-height);
  @include wrapper;

  .image {
    @include display(flex);
    @include flex(0 0);
    @include size(70px);
    margin-right: 20px;
    border-radius: 100%;
    object-fit: cover;
  }

  .info {
    @include display(flex);
    @include flex-direction(column);
    @include justify-content(center);
    @include flex-grow(1);

    .name,
    .description {
      @include display(flex);
      margin: 0;
      font-weight: 400;
    }

    .name {
      font-size: 28px;
      opacity: 0.8;
    }

    .description {
      margin-top: 5px;
      font-size: 14px;
      opacity: 0.6;
    }

  }

  .actions {
    @include display(flex);
    @include align-items(center);
    margin-left: 20px;

    * + * {
      margin-left: 15px;
    }

    .share,
    .sort {
      @include display(flex);
      @include justify-content(center);
      @include align-items(center);
      @include size(36px);
      background: rgba(white, 0.75);
      border: solid 1px rgba(black, 0.1);
      border-radius: 2px;
    }

    .action-icon {
      @include display(flex);
      opacity: 0.75;
    }

  }

}



