@import "../main.scss";

/* Blackboard Theme */

:global(#content) {
  background: url("./images/black.jpg") repeat;
}

:global(.blackboard) {

  .header {

    .info {
      color: white;

      .name,
      .description {
        opacity: 1;
      }

      .name {
        font-family: "Eraser Dust";
        font-size: 28px;
      }

      .description {
        font-family: "Eraser";
        font-size: 20px;
      }

    }

  }

  .board {

    .card {
      margin-top: 20px;
      background: #FFFEEC;
      border-radius: 0;
      z-index: 1;

      &:after {
        @include position(absolute, -20px 0 auto 0);
        @include size(100% 20px);
        margin: auto;
        background: url("./images/paper-cream.svg") repeat;
        background-position: 1px 0;
        content: "";
        z-index: 2;
      }

      .container {
        color: #39454D;
        font-weight: 300;

        .top {

          .author {
            font-weight: 400;
          }

          .delete,
          .restore {
            opacity: 0.5;

            :global(.icon) {

              path {
                fill: #39454D;
              }

            }

            &:hover {
              opacity: 0.75;
            }

          }

        }

        .bottom {

          .like {
            opacity: 0.5;

            .like-counter {
              @include display(flex);
              margin-right: 5px;
              color: #39454D;
            }

            :global(.icon) {

              path {
                fill: #39454D;
              }

            }

            &:global(.liked) {
              opacity: 1;
            }

            &:hover {
              opacity: 0.75;
            }

          }

        }

      }

    }

  }

}



