@import "../main.scss";

/* Simpleboard Theme */

:global(#content) {
  background: #FFDC7A;
}

:global(.simpleboard) {

  .header {
    background: inherit;

    .info {
      color: rgba(black, 0.6);

      .name,
      .description {
        opacity: 1;
      }

      .name {
        font-size: 28px;
      }

      .description {
        font-size: 20px;
      }

    }

  }

  .board {

    .card {
      background: #F8F8F8;
      border-radius: 4px;
      border: solid 1px rgba(black, 0.1);
      z-index: 1;
      box-shadow: 0 5px 5px rgba(black, 0.2);

      .container {
        margin-top: 5px;
        background: inherit;
        color: rgba(black, 0.7);
        border: none;
        position: relative;

        .top,
        .content,
        .bottom {
          position: relative;
          z-index: 2;
        }

        .top {

          .delete,
          .restore {
            opacity: 0.5;

            :global(.icon) {

              path {
                fill: rgba(black, 0.7);
              }

            }

            &:hover {
              opacity: 0.75;
            }

          }

        }

        .bottom {

          .like {
            opacity: 0.5;

            .like-counter {
              @include display(flex);
              margin-right: 5px;
              color: rgba(black, 0.7);
            }

            :global(.icon) {

              path {
                fill: rgba(black, 0.7);
              }

            }

            &:global(.liked) {
              opacity: 1;
            }

            &:hover {
              opacity: 0.75;
            }

          }

        }

      }

    }

  }


}



