html {
  height: 100%;
}

body {
  @include size(100%);
  margin: 0;
  background: #fafafa;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
}

path {
  fill: inherit;
}

button {
  cursor: pointer;
}

:global(#content) {
  @include size(100% auto);
  min-height: 100vh;
}

:global(.button) {
  min-width: 90px;
  padding: 10px 20px;
  border-radius: 4px;
  border: solid 1px transparent;

  background: white;
  color: $blue;
  border-color: currentcolor;

  &:hover {
    background: darken(white, 5%);
  }

  & + :global(.button) {
    margin-left: 10px;
  }

  &:global(.button-primary) {
    background: $blue;
    border-color: $blue;
    color: white;

    &:hover {
      background: darken($blue, 5%);
    }

  }

}



