@import "../../styles/main.scss";

.container {
  @include display(flex);
  @include flex-direction(column);
  @include flex-grow(1);
  width: 100%;
  padding-top: $header-height;
  overflow: hidden;

  @include media($big-mobile) {
    overflow: inherit;
  }

  .board {
    @include display(flex);
    @include flex-direction(column);
    @include flex-grow(1);
    @include wrapper;

    @include media($desktop) {
      max-width: $max-width;
    }

    .grid {
      padding-bottom: 100px;

      @include media($big-mobile) {
        margin: 0 -10px;
        column-count: 2;
        column-gap: 20px;
      }

      @include media($tablet) {
        column-count: 3;
      }

      @include media($big-tablet) {
        column-count: 4;
      }

      @include media($desktop) {
        column-count: 6;
      }

    }

  }

}



