html {
  height: 100%;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  background: #fafafa;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
}

path {
  fill: inherit;
}

button {
  cursor: pointer;
}

:global(#content) {
  height: auto;
  width: 100%;
  min-height: 100vh;
}

:global(.button) {
  min-width: 90px;
  padding: 10px 20px;
  border-radius: 4px;
  border: solid 1px transparent;
  background: white;
  color: #00A8FF;
  border-color: currentcolor;
}

:global(.button):hover {
  background: #f2f2f2;
}

:global(.button) + :global(.button) {
  margin-left: 10px;
}

:global(.button):global(.button-primary) {
  background: #00A8FF;
  border-color: #00A8FF;
  color: white;
}

:global(.button):global(.button-primary):hover {
  background: #0097e6;
}

/* Corckboard Theme */
:global(#content) {
  background: url("./images/corck.jpg") repeat;
}

:global(.corckboard) {
  background-image: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.25) 0%, rgba(255, 255, 255, 0) 20%);
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.25) 0%, rgba(255, 255, 255, 0) 20%);
}

@media screen and (min-width: 670px) {
  :global(.corckboard) {
    background-image: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.5) 0%, rgba(255, 255, 255, 0) 20%);
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5) 0%, rgba(255, 255, 255, 0) 20%);
  }
}

:global(.corckboard) .header .info {
  color: white;
}

:global(.corckboard) .header .info .name,
:global(.corckboard) .header .info .description {
  opacity: 1;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

:global(.corckboard) .header .info .name {
  font-size: 28px;
}

:global(.corckboard) .header .info .description {
  font-size: 20px;
}

:global(.corckboard) .board .card-wrapper {
  -webkit-transform: rotate(1.5deg) scale(1.025);
  -moz-transform: rotate(1.5deg) scale(1.025);
  -ms-transform: rotate(1.5deg) scale(1.025);
  -o-transform: rotate(1.5deg) scale(1.025);
  transform: rotate(1.5deg) scale(1.025);
  padding-top: 20px;
  margin-bottom: -10px;
}

:global(.corckboard) .board .card-wrapper:after {
  position: absolute;
  top: 5px;
  right: 0;
  bottom: auto;
  left: 0;
  height: 53px;
  width: 29px;
  margin: auto;
  background: url("./images/pin-1.png") center no-repeat;
  background-size: contain;
  content: "";
  z-index: 2;
}

:global(.corckboard) .board .card-wrapper:nth-child(3n+1) {
  -webkit-transform: rotate(-1deg) scale(1.025);
  -moz-transform: rotate(-1deg) scale(1.025);
  -ms-transform: rotate(-1deg) scale(1.025);
  -o-transform: rotate(-1deg) scale(1.025);
  transform: rotate(-1deg) scale(1.025);
}

:global(.corckboard) .board .card-wrapper:nth-child(3n+1):after {
  background-image: url("./images/pin-2.png");
}

:global(.corckboard) .board .card-wrapper:nth-child(5n+1):after {
  background-image: url("./images/pin-3.png");
}

:global(.corckboard) .board .card {
  background: #0099E6;
  border-radius: 4px;
  z-index: 1;
}

:global(.corckboard) .board .card .container {
  color: rgba(255, 255, 255, 0.75);
  font-weight: 300;
}

:global(.corckboard) .board .card .container .top .delete,
:global(.corckboard) .board .card .container .top .restore {
  opacity: 0.5;
}

:global(.corckboard) .board .card .container .top .delete :global(.icon) path,
:global(.corckboard) .board .card .container .top .restore :global(.icon) path {
  fill: white;
}

:global(.corckboard) .board .card .container .top .delete:hover,
:global(.corckboard) .board .card .container .top .restore:hover {
  opacity: 0.75;
}

:global(.corckboard) .board .card .container .bottom .like {
  opacity: 0.5;
}

:global(.corckboard) .board .card .container .bottom .like .like-counter {
  display: -webkit-box;
  display: -moz-box;
  display: box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  margin-right: 5px;
  color: inherit;
}

:global(.corckboard) .board .card .container .bottom .like :global(.icon) path {
  fill: white;
}

:global(.corckboard) .board .card .container .bottom .like:global(.liked) {
  opacity: 1;
}

:global(.corckboard) .board .card .container .bottom .like:hover {
  opacity: 0.75;
}



