html {
  height: 100%;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  background: #fafafa;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
}

path {
  fill: inherit;
}

button {
  cursor: pointer;
}

:global(#content) {
  height: auto;
  width: 100%;
  min-height: 100vh;
}

:global(.button) {
  min-width: 90px;
  padding: 10px 20px;
  border-radius: 4px;
  border: solid 1px transparent;
  background: white;
  color: #00A8FF;
  border-color: currentcolor;
}

:global(.button):hover {
  background: #f2f2f2;
}

:global(.button) + :global(.button) {
  margin-left: 10px;
}

:global(.button):global(.button-primary) {
  background: #00A8FF;
  border-color: #00A8FF;
  color: white;
}

:global(.button):global(.button-primary):hover {
  background: #0097e6;
}

/* Simpleboard Theme */
:global(#content) {
  background: #FFDC7A;
}

:global(.simpleboard) .header {
  background: inherit;
}

:global(.simpleboard) .header .info {
  color: rgba(0, 0, 0, 0.6);
}

:global(.simpleboard) .header .info .name,
:global(.simpleboard) .header .info .description {
  opacity: 1;
}

:global(.simpleboard) .header .info .name {
  font-size: 28px;
}

:global(.simpleboard) .header .info .description {
  font-size: 20px;
}

:global(.simpleboard) .board .card {
  background: #F8F8F8;
  border-radius: 4px;
  border: solid 1px rgba(0, 0, 0, 0.1);
  z-index: 1;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
}

:global(.simpleboard) .board .card .container {
  margin-top: 5px;
  background: inherit;
  color: rgba(0, 0, 0, 0.7);
  border: none;
  position: relative;
}

:global(.simpleboard) .board .card .container .top,
:global(.simpleboard) .board .card .container .content,
:global(.simpleboard) .board .card .container .bottom {
  position: relative;
  z-index: 2;
}

:global(.simpleboard) .board .card .container .top .delete,
:global(.simpleboard) .board .card .container .top .restore {
  opacity: 0.5;
}

:global(.simpleboard) .board .card .container .top .delete :global(.icon) path,
:global(.simpleboard) .board .card .container .top .restore :global(.icon) path {
  fill: rgba(0, 0, 0, 0.7);
}

:global(.simpleboard) .board .card .container .top .delete:hover,
:global(.simpleboard) .board .card .container .top .restore:hover {
  opacity: 0.75;
}

:global(.simpleboard) .board .card .container .bottom .like {
  opacity: 0.5;
}

:global(.simpleboard) .board .card .container .bottom .like .like-counter {
  display: -webkit-box;
  display: -moz-box;
  display: box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  margin-right: 5px;
  color: rgba(0, 0, 0, 0.7);
}

:global(.simpleboard) .board .card .container .bottom .like :global(.icon) path {
  fill: rgba(0, 0, 0, 0.7);
}

:global(.simpleboard) .board .card .container .bottom .like:global(.liked) {
  opacity: 1;
}

:global(.simpleboard) .board .card .container .bottom .like:hover {
  opacity: 0.75;
}



