@import "../main.scss";

/* Corckboard Theme */

:global(#content) {
  background: url("./images/corck.jpg") repeat;
}

:global(.corckboard) {
  @include background-image(linear-gradient(top, rgba(black,0.25) 0%,rgba(white,0) 20%));

  @include media($tablet) {
    @include background-image(linear-gradient(top, rgba(black,0.5) 0%,rgba(white,0) 20%));
  }

  .header {

    .info {
      color: white;

      .name,
      .description {
        opacity: 1;
        text-shadow: 0 2px 4px rgba(black, .3);
      }

      .name {
        font-size: 28px;
      }

      .description {
        font-size: 20px;
      }

    }

  }

  .board {

    .card-wrapper {
      @include transform(rotate(1.5deg) scale(1.025));
      padding-top: 20px;
      margin-bottom: -10px;

      &:after {
        @include position(absolute, 5px 0 auto 0);
        @include size(29px 53px);
        margin: auto;
        background: url("./images/pin-1.png") center no-repeat;
        background-size: contain;
        content: "";
        z-index: 2;
      }

      &:nth-child(3n+1) {
        @include transform(rotate(-1deg) scale(1.025));

        &:after {
          background-image: url("./images/pin-2.png");
        }

      }

      &:nth-child(5n+1) {

        &:after {
          background-image: url("./images/pin-3.png");
        }

      }

    }

    .card {
      background: #0099E6;
      border-radius: 4px;
      z-index: 1;

      .container {
        color: rgba(white, 0.75);
        font-weight: 300;

        .top {

          .delete,
          .restore {
            opacity: 0.5;

            :global(.icon) {

              path {
                fill: white;
              }

            }

            &:hover {
              opacity: 0.75;
            }

          }

        }

        .bottom {

          .like {
            // @include transition(all 200ms);
            opacity: 0.5;

            .like-counter {
              @include display(flex);
              margin-right: 5px;
              color: inherit;
            }

            :global(.icon) {

              path {
                fill: white;
              }

            }

            &:global(.liked) {
              opacity: 1;
            }

            &:hover {
              opacity: 0.75;
            }

          }

        }

      }

    }

  }

}



