@import "../../styles/main.scss";
.card-wrapper {
  display: block;
  margin-bottom: 20px;
  width: 100%;

  @include media($big-mobile) {
    display: inline-block;
  }

  @include media($desktop) {
    width: 210px;
  }

}
.card {
  position: relative;
  margin: 0 10px 20px;
  padding: 10px;
  border-radius: 3px;
  color: #5E5E5E;
  break-inside: avoid-column;

  .container {
    break-inside: avoid-column;
    display: inline-block;
    width: 100%;
    .top {
      @include display(flex);
      @include flex-direction(row);
      @include align-items(center);
      margin-bottom: 5px;

      .author {
        @include display(flex);
        @include flex-grow(1);
        font-size: 12px;
      }

      .delete,
      .restore {
        $size: 16px;
        @include display(flex);
        @include flex(0 0 $size);
        padding: 0;
        border: none;
        background: transparent;
        outline: none;

        :global(.icon) {
          @include size(16px);
          @include transition(all 200ms);
        }

      }

      .delete {
        margin-left: 2px;
      }

      .hidden {
        display: none;
      }

    }

    .content {
      @include display(flex);
      @include flex-direction(column);
      @include flex-grow(1);

      .text {
        @include display(flex);
        margin: 0;
        font-size: 14px;
        word-break: break-word;
        overflow: hidden;
      }

      .image {
        @include display(flex);
        margin: 5px 0;
        max-width: 100%;
      }

    }

    .bottom {
      @include display(flex);
      @include flex-direction(row-reverse);
      margin-top: 5px;

      .like {
        @include display(flex);
        @include align-items(center);
        padding: 0;
        color: white;
        font-weight: 300;
        cursor: pointer;
        background: transparent;
        border: none;

        .like-counter {
          @include display(flex);
          margin-right: 5px;
        }

        .like-icon {
          @include display(flex);
          @include size(16px);
          @include transition(all 200ms);
        }

      }

    }

    .faded {
      opacity: 0.3;
      pointer-events: none;
    }

    .invisible {
      visibility: hidden;
      pointer-events: none;
    }

  }

}



