html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

@font-face {
  font-family: "Avenir";
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/avenir/AvenirLTStd-Light.woff") format("woff");
}

@font-face {
  font-family: "Avenir";
  font-style: normal;
  font-weight: 300;
  src: url("/fonts/avenir/AvenirLTStd-Roman.woff") format("woff");
}

@font-face {
  font-family: "Avenir";
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/avenir/AvenirLTStd-Heavy.woff") format("woff");
}

