html {
  height: 100%;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  background: #fafafa;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
}

path {
  fill: inherit;
}

button {
  cursor: pointer;
}

:global(#content) {
  height: auto;
  width: 100%;
  min-height: 100vh;
}

:global(.button) {
  min-width: 90px;
  padding: 10px 20px;
  border-radius: 4px;
  border: solid 1px transparent;
  background: white;
  color: #00A8FF;
  border-color: currentcolor;
}

:global(.button):hover {
  background: #f2f2f2;
}

:global(.button) + :global(.button) {
  margin-left: 10px;
}

:global(.button):global(.button-primary) {
  background: #00A8FF;
  border-color: #00A8FF;
  color: white;
}

:global(.button):global(.button-primary):hover {
  background: #0097e6;
}

/* Foamboard Theme */
:global(#content) {
  background: url("./images/foam.png") repeat #5B81C5;
}

:global(.foamboard) .header {
  background: inherit;
}

:global(.foamboard) .header .info {
  color: white;
}

:global(.foamboard) .header .info .name,
:global(.foamboard) .header .info .description {
  opacity: 1;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

:global(.foamboard) .header .info .name {
  font-size: 28px;
}

:global(.foamboard) .header .info .description {
  font-size: 20px;
}

:global(.foamboard) .board .card-wrapper {
  -webkit-transform: rotate(1.5deg) scale(1.025);
  -moz-transform: rotate(1.5deg) scale(1.025);
  -ms-transform: rotate(1.5deg) scale(1.025);
  -o-transform: rotate(1.5deg) scale(1.025);
  transform: rotate(1.5deg) scale(1.025);
  padding-top: 10px;
}

:global(.foamboard) .board .card-wrapper:nth-child(3n+1) {
  -webkit-transform: rotate(-1deg) scale(1.025);
  -moz-transform: rotate(-1deg) scale(1.025);
  -ms-transform: rotate(-1deg) scale(1.025);
  -o-transform: rotate(-1deg) scale(1.025);
  transform: rotate(-1deg) scale(1.025);
}

:global(.foamboard) .board .card {
  background: #B94F47;
  border-radius: 4px;
  z-index: 1;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
}

:global(.foamboard) .board .card:after {
  position: absolute;
  top: -5px;
  right: 0;
  bottom: auto;
  left: 0;
  height: 15px;
  width: 15px;
  margin: auto;
  background: url("./images/pin.png") center no-repeat;
  background-size: contain;
  content: "";
  z-index: 2;
}

:global(.foamboard) .board .card .container {
  margin-top: 5px;
  background: inherit;
  color: white;
  border: none;
  position: relative;
}

:global(.foamboard) .board .card .container .top,
:global(.foamboard) .board .card .container .content,
:global(.foamboard) .board .card .container .bottom {
  position: relative;
  z-index: 2;
}

:global(.foamboard) .board .card .container .top .delete,
:global(.foamboard) .board .card .container .top .restore {
  -webkit-transition: all 200ms;
  -moz-transition: all 200ms;
  transition: all 200ms;
  opacity: 0.5;
}

:global(.foamboard) .board .card .container .top .delete :global(.icon) path,
:global(.foamboard) .board .card .container .top .restore :global(.icon) path {
  fill: white;
}

:global(.foamboard) .board .card .container .top .delete:hover,
:global(.foamboard) .board .card .container .top .restore:hover {
  opacity: 0.75;
}

:global(.foamboard) .board .card .container .bottom .like {
  opacity: 0.5;
}

:global(.foamboard) .board .card .container .bottom .like .like-counter {
  display: -webkit-box;
  display: -moz-box;
  display: box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  margin-right: 5px;
  color: inherit;
}

:global(.foamboard) .board .card .container .bottom .like :global(.icon) path {
  fill: white;
}

:global(.foamboard) .board .card .container .bottom .like:global(.liked) {
  opacity: 1;
}

:global(.foamboard) .board .card .container .bottom .like:hover {
  opacity: 0.75;
}

:global(.foamboard) .board .card:nth-child(even):after {
  -webkit-transform: scaleX(-1);
  -moz-transform: scaleX(-1);
  -ms-transform: scaleX(-1);
  -o-transform: scaleX(-1);
  transform: scaleX(-1);
}



