@import "../../styles/main.scss";

.themePreview {
  list-style: none;
  display: inline-block;
  width: 100px;
  height: 100px;
  margin-right: 10px;

  &:last-child {
    margin-right: 0;
  }
}

.themeImage {
  @include size(100%);
  cursor: pointer;
  border-radius: 3px;
}

.themeItem {
  position: relative;
  border: solid 2px transparent;
  border-radius: 5px;

  &.activeTheme {
    border-color: $blue;
  }
}

.themeSelected {
  $size: 34px;
  @include position(absolute, 0 0 0 0);
  margin: auto;
  pointer-events: none;
  background: url('./Check.png') rgba(black, 0.35) no-repeat center;
  background-size: 34px;
  opacity: 0;
}

.activeTheme .themeSelected {
  opacity: 1;
}



