@import "../../styles/main.scss";

.overlay {
  @include position(fixed, 0 0 0 0);
  @include align-items(center);
  @include justify-content(center);
  background-color: rgba(21, 45, 57, 0.9);
  z-index: 1000;
  display: none;
  &.showModal {
    @include display(flex);
  }
}

.modalContainer {
  @include display(flex);
  @include flex-direction(column);
  @include align-items(center);
  background-color: #FFF;
  width: 100%;
  height: 100%;
  max-width: 520px;
  max-height: 540px;
  border-radius: 3px;
  padding: 30px;
  .modalTitle {
    font-size: 16px;
    font-weight: normal;
  }
  .searchBar {
    width: 100%;
    @include display(flex);
    @include align-items(stretch);
    .googleIcon {
      background: aliceblue;
      @include display(flex);
      @include flex(0 0 50px);
      @include size(50px);
    }
    .searchWidget {
      @include display(flex);
      @include align-items(stretch);
      @include flex-grow(1);
      position: relative;

      .searchInput {
        @include display(flex);
        @include flex-grow(1);
      }
      .searchButton {
        @include display(flex);
        @include flex(0 0 50px);
        @include size(50px);
      }
      .searchImages {
        position: absolute !important;
        z-index: 1000;
        left: 0 !important;
        top: 50px;
        right: 0 !important;
        max-height: 350px !important;
        display: none;
        @include flex-wrap(wrap);
        @include align-items(flex-start);
        background-color: white;
        list-style: none;
        margin: 0;
        padding: 5px;
        border: 1px solid #777;
        box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
        overflow: auto;
        .resultItem {
          max-width: 30%;
          min-width: 30%;
          padding: 5px;
          .resultImage {
            max-width: 100%;
          }
        }
        &.showGoogleResults {
          @include display(flex);
        }
      }
    }
  }
  .dropArea {
    .dropImage {
      @include display(flex);
      @include flex(0 0 130px);
      @include size(130px);
      @include flex-direction(column);
      position: relative;
      :global(.dz-clickable) {
        outline: 1px solid red;
        @include position(absolute, 0 0 0 0);
      }
      :global(.dz-image-preview) {
        pointer-events: none;
      }
      :global(img) {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        pointer-events: none;
      }
    }
  }
  .divisor {
    .divisorText {

    }
  }
  .inputSource {
    .inputSourceTitle {

    }
    .inputSourceButtons {
      @include display(flex);
      @include justify-content(center);
      .fileSource {

      }
      .pictureSource {

      }
    }
  }

  .modalActionButtons {
    .modalActionCancel {

    }
    .modalActionSave {

    }
  }
}


