@import "../../styles/main.scss";

.compose {
  @include position(fixed, auto 0 0 0);
  @include display(flex);
  @include flex(0 0);
  @include size(100% $compose-height);
  margin: 0 auto;
  padding: 0 10px;
  z-index: 5;
  border-top: solid 1px rgba(black, 0.1);
  background: white;

  @include media($big-tablet) {
    width: $big-tablet-width;
    border-radius: 5px;
    bottom: 10px;
    box-shadow: 0 3px 4px rgba(black, 0.25);
  }

  .form {
    @include display(flex);
    @include align-items(center);
    @include wrapper;
    color: lighten(black, 75%);
    padding: 10px 0;

    * + * {
      margin-left: 10px;
    }

    .input {
      @include display(flex);
      @include flex-grow(1);
      @include align-self(stretch);
      border: none;
      font-size: 14px;
      font-weight: 300;
      color: inherit;
      outline: none;
      letter-spacing: 0.5px;
    }

    .counter {
      @include display(flex);
      @include flex(0 0);

      &.exceeded {
        color: red;
      }

    }

    .image {
      @include display(flex);
      @include align-self(stretch);
      @include justify-content(center);
      width: 40px;
      background: $blue;
      border: none;
      border-radius: 4px;

      &:hover {
        background: lighten($blue, 10%);
      }

    }

    .send {
      @include display(flex);
      @include align-self(stretch);
      @include justify-content(center);
      width: 120px;
      background: $blue;
      border: none;
      border-radius: 4px;
      color: white;
      font-weight: 300;

      &.disabled {
        background: lighten(black, 75%);
        opacity: 0.5;
        pointer-events: none;
      }

      &:hover {
        background: lighten(black, 65%);
      }

    }

  }

}



