html {
  height: 100%;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  background: #fafafa;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
}

path {
  fill: inherit;
}

button {
  cursor: pointer;
}

:global(#content) {
  height: auto;
  width: 100%;
  min-height: 100vh;
}

:global(.button) {
  min-width: 90px;
  padding: 10px 20px;
  border-radius: 4px;
  border: solid 1px transparent;
  background: white;
  color: #00A8FF;
  border-color: currentcolor;
}

:global(.button):hover {
  background: #f2f2f2;
}

:global(.button) + :global(.button) {
  margin-left: 10px;
}

:global(.button):global(.button-primary) {
  background: #00A8FF;
  border-color: #00A8FF;
  color: white;
}

:global(.button):global(.button-primary):hover {
  background: #0097e6;
}

.themePreview {
  list-style: none;
  display: inline-block;
  width: 100px;
  height: 100px;
  margin-right: 10px;
}

.themePreview:last-child {
  margin-right: 0;
}

.themeImage {
  height: 100%;
  width: 100%;
  cursor: pointer;
  border-radius: 3px;
}

.themeItem {
  position: relative;
  border: solid 2px transparent;
  border-radius: 5px;
}

.themeItem.activeTheme {
  border-color: #00A8FF;
}

.themeSelected {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  pointer-events: none;
  background: url("./Check.png") rgba(0, 0, 0, 0.35) no-repeat center;
  background-size: 34px;
  opacity: 0;
}

.activeTheme .themeSelected {
  opacity: 1;
}



